import { MetaData } from './common/common.model';

export class Payment {
    public paymentType: string = "";
}

export class CardPayment extends Payment {
    public cardNumber: string = "";
    public expDate: string = "";
    public cvvCode: string = ""; 

    constructor() {
        super();
        this.paymentType = "cc";
    }

    get formattedCardNumber(): string{
        return this.cardNumber && this.cardNumber.length > 0? this.cardNumber.replace(/X/g, '*'): this.cardNumber;
    }

    get formattedExpDate(): string{
        return this.expDate && this.expDate.length == 7 ? this.expDate.substring(5) + "/" + this.expDate.substring(2, 4) : this.expDate;        
    }
}

export class BankAccountType {
    public name: string;
    public code: number;

    constructor(name: string, code: number) {
        this.name = name;
        this.code = code;
    }
}

export class BankPayment extends Payment {
    public accountNumber: string = "";
    public routingNumber: string = "";
    public nameOnAccount: string = "";
    public bankName: string = "";
    public bankAccountType: BankAccountType;

    constructor() {
        super();
        this.paymentType = "echeck";
        this.bankAccountType = new BankAccountType("Personal Checking", 0);
    }

    get formattedAccountNumber(): string{
        return this.accountNumber && this.accountNumber.length > 0? this.accountNumber.replace(/X/g, '*'): this.accountNumber;
    }
    get formattedRoutingNumber(): string{
        return this.routingNumber && this.routingNumber.length > 0? this.routingNumber.replace(/X/g, '*'): this.routingNumber;
    }
}

export class ProfilePayment extends Payment {
    public paymentProfileId: string = ""; 
    public cvvCode: string = ""; 
    constructor() {
        super();
        this.paymentType = "paymentProfile";
    }
}

export class CardPaymentMetaData {
    public cardNumber: MetaData;
    public expDate: MetaData;
    public cvvCode: MetaData;

    constructor() {
        this.cardNumber = new MetaData();
        this.expDate = new MetaData();
        this.cvvCode = new MetaData();
        this.initializeCardPaymentMetaData();
    }

    private initializeCardPaymentMetaData(): void {
        this.cardNumber.label = "Card Number";
        this.cardNumber.placeholder = "1234 5678 9012 3456";
        this.expDate.label = "Exp. Date";
        this.expDate.placeholder = "MM/YY";
        this.cvvCode.label = "Card Code";
        this.cvvCode.placeholder = "CVV";
    }
}

export class BankPaymentMetaData {
    public accountNumber: MetaData;
    public routingNumber: MetaData;
    public nameOnAccount: MetaData;
    public bankName: MetaData;
    public bankAccountType: MetaData;

    constructor() {
        this.accountNumber = new MetaData();
        this.routingNumber = new MetaData();
        this.nameOnAccount = new MetaData();
        this.bankName = new MetaData();
        this.bankAccountType = new MetaData();
        this.initializeBankPaymentMetaData();
    }

    private initializeBankPaymentMetaData(): void {
        this.accountNumber.label = "Account Number";
        this.accountNumber.placeholder = "Up To 17 Digits";
        this.routingNumber.label = "ABA Routing Number";
        this.routingNumber.placeholder = "9 Digits";
        this.nameOnAccount.label = "Name On Account";
        this.nameOnAccount.placeholder = "Name On Account";
        this.bankName.label = "Bank Name";
        this.bankName.placeholder = "Bank Name";
        this.bankAccountType.label = "Bank Account Type";
        this.bankAccountType.placeholder = "Bank Account Type";
    }
}



