import { Component, OnInit, Input, Output, EventEmitter, HostListener, ViewEncapsulation, ViewChild, ElementRef } from '@angular/core';
import { DEFAULT_VALUES } from '../../../models/static.model';

let aria = { KeyCode : {
  BACKSPACE: 8,
  TAB: 9,
  RETURN: 13,
  ESC: 27,
  SPACE: 32,
  PAGE_UP: 33,
  PAGE_DOWN: 34,
  END: 35,
  HOME: 36,
  LEFT: 37,
  UP: 38,
  RIGHT: 39,
  DOWN: 40,
  DELETE: 46
}};

@Component({
  selector: 'anet-drop-down',
  templateUrl: './dropdown.component.html',
  styleUrls: ['./dropdown.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class DropdownComponent implements OnInit {

  // Dropdown need below Inputs or it may throw exception
  @Input() metaData: any;
  @Input() customCSS: any = {};
  @Input() itemList: any;
  @Input() selectedItem: any;
  @Input() componentUniqueID: string;
  @Output() selectedItemChange = new EventEmitter<any>();

  changedItemList: any;
  selectedCountry: any;
  hasFocus: boolean = false;
  listboxFocus: boolean = false;
  activeDescendantID: string;
  selectedIndex: number;
  keySoFar:string;
  keyClear: any;

  constructor() {

  }

  ngOnInit() {

    this.keySoFar = "";
    this.changedItemList = this.itemList;
    this.componentUniqueID = this.componentUniqueID !== undefined ? this.componentUniqueID : '';

    if(this.selectedItem){
      let index = this.changedItemList.findIndex(fi => fi.code == this.selectedItem.code);
      this.activeDescendantID = `${this.componentUniqueID}Item_${index}`;
      this.selectedIndex = index;
    }
    else
    {
      //default
      this.activeDescendantID = `${this.componentUniqueID}Item_0`;
      this.selectedIndex = 0;
    }
    
    this.initializeListboxFocus();
  
  }

  // ON BUTTON CLICK Select Title
  onClickSelectedTitle(evt, listboxNode) {

    evt.preventDefault();

    setTimeout(() => {
      document.getElementById(listboxNode.id).focus();
      this.centerOnListbox(listboxNode);
    }, 10);

  }

  // capture known keydowns
  selectTitleKeydown(evt, selectTitle){

    // Only Event.preventDefault() if we have 2. Otherwise
    // some functionality is loss.

    let key = evt.which || evt.keyCode;

    switch (key) {
      case aria.KeyCode.UP:
      case aria.KeyCode.DOWN:
        evt.preventDefault();
        evt.stopPropagation();
      break;
      case aria.KeyCode.RETURN:
      case aria.KeyCode.SPACE:
        selectTitle.click();
        selectTitle.focus();
        evt.preventDefault();
      break;
    }

  }

  handleKeyboardEvent(keyPressed, listboxNode) {

    let charRegx = /^[A-Za-z]+$/;
    let charKeyPressed = String.fromCharCode(keyPressed).toLowerCase().toString();

    if(charRegx.test(charKeyPressed) ){
        this.changedItemList = this.itemList;
        this.keySoFar += charKeyPressed.toLocaleLowerCase();
        this.clearKeysSoFarAfterDelay();
        
        let index = this.findItemIndex(this.keySoFar) ; 
        if(index >= 0){
            this.selectedIndex = index;
            setTimeout(() => {
              this.centerOnListbox(listboxNode);
            }, 10);
        };
         
    }

  }

  findItemIndex(keySoFar: string) {

    var index = this.changedItemList.findIndex((item):boolean => {
    
      let name = item.name.toLocaleLowerCase();
      return (name.length >= keySoFar.length) && (name.substring(0, keySoFar.length) === keySoFar); 
    
    });
    
    return index;
   
  }

  clearKeysSoFarAfterDelay (){
  
    // if we keyClear before 500ms it never files.
    if (this.keyClear) {
      clearTimeout(this.keyClear);
      this.keyClear = null;
    }
  
    this.keyClear = setTimeout((function () {
      this.keySoFar = '';
      this.keyClear = null;
    }).bind(this), 500);
  
  }

  focusOnListbox(lbn){

    // dont continue if we're not scrolling
    if (lbn.scrollHeight === lbn.clientHeight) {
      return;
    }

    let element = lbn.getElementsByClassName("active")[0]; // should always be 1
    let scrollTop = lbn.scrollTop;
    let scrollBottom = lbn.scrollTop + lbn.clientHeight;
    let elementBottom = element.offsetTop + element.offsetHeight;
  
    // if elementBottom.px is lower than scrollBottom.px then match tops
    // OR if elementBottom is higher than the scrollTop then match tops
    if ( scrollBottom < elementBottom || elementBottom <= scrollTop) {
        lbn.scrollTop = element.offsetTop;
    }

  }

  centerOnListbox(lbn) {

    // dont continue if we're not scrolling
    if (lbn.scrollHeight === lbn.clientHeight) {
      return;
    }

    let element = lbn.getElementsByClassName("active")[0]; // should always be 1
    lbn.scrollTop = element.offsetTop - (lbn.clientHeight/2) + (element.offsetHeight*2);

  }

  // ListBox Keypress handler
  checkKeyPress(evt, listboxNode) {

    let key = evt.which || evt.keyCode;
    let si:number = (this.selectedIndex*1);
    let ll:number = ((this.changedItemList.length*1)-1);

    switch (key) {
      case aria.KeyCode.UP:
      case aria.KeyCode.DOWN:
        if (key === aria.KeyCode.UP) {
          // check for min
          this.selectedIndex = si > 0 ? si - 1 : si;
        }
        else {
          // check for max.
          this.selectedIndex = si < ll ? si + 1 : ll;
        }
        
        this.activeDescendantID = `${this.componentUniqueID}Item_${this.selectedIndex}`;

        setTimeout(() => {
          this.focusOnListbox(listboxNode);
        }, 10);

        break;
      case aria.KeyCode.RETURN: // AKA ENTER
      //case aria.KeyCode.SPACE: // Space only opens. Doesn't close the dropdown

        let data = this.changedItemList[si];
        this.selectedItemChange.emit(data);

        let st = document.getElementById(this.componentUniqueID+"_selectTitle");
        st.click();
        st.focus();

        //we're selecting dropdown item so no focus
        this.listboxFocus = false;

        break;
      default:
        // nothing default
        this.handleKeyboardEvent(key, listboxNode)
        break;
    
    }
  
    evt.preventDefault();

  }

  // UL \ LI mouse click event handler
  onItemClick(evt) {
    
    let index = evt.target.id.split("_")[1];
    let item = this.changedItemList[index];
    this.selectedIndex = (index*1);
    this.activeDescendantID = `${this.componentUniqueID}Item_${index}`;
    this.selectedItemChange.emit(item);

    this.focusItem(this.componentUniqueID+"_selectTitle");

    //we're clicking the dropdown so no focus
    this.listboxFocus = false;

    evt.preventDefault();

  }
  
  setCustomborderStyle(): {} {
    return this.hasFocus ? this.customCSS.input : { 'border-bottom': DEFAULT_VALUES.DEFAULT_BORDER_BOTTOM };
  }

  focusItem(elemID){
    setTimeout(() => {
      document.getElementById(elemID).focus();
    }, 10);
  }

  // dropdown focus (coming to button)
  onFocus() {
    this.hasFocus = true;
  }

  // dropdown focus (leaving button)
  onBlur() {
     this.hasFocus = this.listboxFocus;
  }

  initializeListboxFocus(){

    // JRS I dont like setting the timeout each time to update the dom. 
    // Wonder if its behind the angular lifecycle just enough
    // Up for suggestions.
    setTimeout(() => {

      let lb:HTMLElement = document.getElementById(this.componentUniqueID+"_listboxNode");
      lb.onfocus = () => {
        this.listboxFocus = true;
        this.hasFocus = true;
      };
      lb.onblur = (evt:FocusEvent) => {
        this.listboxFocus = false;

        // IF the srcElement IS the listbox's parent button 
        // THEN focus ELSE the user clicked on something else on the form  
        this.hasFocus = (evt.srcElement.id == this.componentUniqueID+"_selectTitle");
      };

    }, 10);

  }

  getTitle(){

    // firefox does not show the value for aria. So we add it here in the title.
    var isFirefox = navigator.userAgent.toLowerCase().indexOf('firefox') > -1;
    return this.metaData.label + (isFirefox ? (" " + this.selectedItem.name) : "");

  }

}



