/// <summary>
/// this class is for display options only, should not be treated as domain models for security reason
/// </summary>
export class MetaData {
    public required: boolean = false;   // if true, the asterick mark will be displayed on UI to indicate this input field needs to be filled by user
    public label: string = "";
    public placeholder: string = "";    // default text to display inside the input field
    public show : boolean = true;
}

export class VisaCheckoutOptions {
    public apiKey: string = "";
    public displayName: string = "";
    public message: string = "";
    public collectShipping: boolean = false;
    public totalAmount: string = "";
    public currencyCode: string = "USD";
    public visaCheckoutUrl: string = "";
}

/// <summary>
/// 
/// </summary>
export enum Operation {
    create = 0,
    read   = 1,
    update = 2,
    delete = 3
}

export enum CallerPage {
    manage   = 0,
    payment  = 1,
    shipping = 2
}


