import { Component, OnInit, OnChanges, Injectable, Input,  EventEmitter, Output, SimpleChanges } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { StaticDataSource, DEFAULT_VALUES } from '../../../models/static.model';
import { PhonePipe, CapitalizeFirstPipe } from '../../../pipes/common.pipes';
import { DropdownComponent } from '../../dropdown/v1/dropdown.component';
import { LocationService } from '../../../services/location.service';
import { Address, AddressMetaData } from '../../../models/address.model';

@Component({
	selector: 'address-component',
	templateUrl: './address.component.html',
	styleUrls: ['./address.component.scss'],
	providers: [LocationService, StaticDataSource]
})
export class AddressComponent implements OnInit, OnChanges {

	hasFirstNameFocus;
	hasLastNameFocus;
	hasZipFocus;
	hasAddressFocus;
	hasCityFocus;
	hasStateFocus;
	hasPhoneFocus;
	dropdownObject:DropdownComponent;
	countries: any;
	overwritingCss:string;
	allowedCssBluePrint:any;

	@Input() model: Address;
	@Input() metaData: AddressMetaData;
	@Input() customCss: string;
	@Input() componentUniqueID: string;
	@Output() isComponentValid = new EventEmitter<boolean>();
	
	constructor(private locationService: LocationService, private staticDataSource:StaticDataSource) {
		this.countries = staticDataSource.getCountryList();
	}

	ngOnInit() {

		this.componentUniqueID = this.componentUniqueID !== undefined ? this.componentUniqueID : '';

		this.overwritingCss = (typeof this.customCss!=="undefined" && this.customCss) || DEFAULT_VALUES.BACKGROUND_COLOR;

		this.allowedCssBluePrint = {
			"firstNameID": {
				"input": {
					'border-bottom': '4px solid ' + this.overwritingCss
				},
				"label": {
					'color': this.overwritingCss
				}
			},
			"lastNameID": {
				"input": {
					'border-bottom': '4px solid ' + this.overwritingCss
				},
				"label": {
					'color': this.overwritingCss
				}

			},
			"zipID": {
				"input": {
					'border-bottom': '4px solid ' + this.overwritingCss
				},
				"label": {
					'color': this.overwritingCss
				}
			},
			"addressID": {
				"input": {
					'border-bottom': '4px solid ' + this.overwritingCss
				},
				"label": {
					'color': this.overwritingCss
				}
			},
			"cityID": {
				"input": {
					'border-bottom': '4px solid ' + this.overwritingCss
				},
				"label": {
					'color': this.overwritingCss
				}
			},
			"stateID": {
				"input": {
					'border-bottom': '4px solid ' + this.overwritingCss
				},
				"label": {
					'color': this.overwritingCss
				}
			},
			"countryID": {
				"input": {
					'border-bottom': '4px solid ' + this.overwritingCss
				},
				"label": {
					'color': this.overwritingCss
				}
			},
			"phoneNumberID": {
				"input": {
					'border-bottom': '4px solid ' + this.overwritingCss
				},
				"label": {
					'color': this.overwritingCss
				}
			}
		};
	} 

	ngOnChanges(changes: SimpleChanges): void {
		if (changes['model']) {
			this.isValidAddress();	
		}
	}

	isValidAddress() {
		let status: boolean = false;
		if (this.model !== undefined && this.metaData != undefined) {
			status = this.isValidInput(this.model.firstName, this.metaData.firstName);
			status = status && this.isValidInput(this.model.lastName, this.metaData.lastName);
			status = status && this.isValidInput(this.model.zip, this.metaData.zip);
			
			if (this.metaData !== undefined && this.metaData.isShort == false) {

				status = status && this.isValidInput(this.model.address, this.metaData.address);
				status = status && this.isValidInput(this.model.city, this.metaData.city);
				status = status && this.isValidInput(this.model.state, this.metaData.state);
				status = status && this.isValidInput(this.model.country.code, this.metaData.country);
				if(this.showPhoneField()){
					status = status && this.isValidInput(this.model.phoneNumber, this.metaData.phoneNumber);
				}
			}
		}
		this.isComponentValid.emit(status);
		return status;
	}

	isValidInput(value : any, metaData: any)
	{
		if(metaData !== undefined && metaData.required)
			return (value.trim().length > 0);
		else
			return true;
	}

	isValidChars(event) {
		let reg: any = /^[!@#$%^&*)\/(']/;
		let key = String.fromCharCode(!event.charCode ? event.which : event.charCode);
		if (reg.test(key)) {
			event.preventDefault();
			return false;			
		}
	
		return true;
	}

	isValidZipCode(event) {
		let reg: any = /^[`~!@#$%^&*()_+={}<>,.;:'"|\[\]\?\/\\]*$/;
		let key = String.fromCharCode(!event.charCode ? event.which : event.charCode);
		if (reg.test(key)) {
			event.preventDefault();
			return false;
		}
		return true;
	}
	
	isValidPhoneNumber(event) {
		let reg: any = /[^0-9\s-+()\b]/;
		let key = String.fromCharCode(!event.charCode ? event.which : event.charCode);
		
		// "\u0000" is tab.
		// This function is different than others. Its regex is negative lookup. 
		if (reg.test(key) && key!=="\u0000") {
			event.preventDefault();
			return false;
		}
		return true;
	}

	validateZipCode() {
		if (this.model.country.code === 'US' && this.model.zip && this.model.zip.length == 5) {
			var response = this.locationService.getCityAndZip(this.model.zip)
				.subscribe(result => {
					if (result.status === 'OK') {
						if (result.results.length == 1) {
							if (result.results[0].formatted_address !== undefined && result.results[0].formatted_address.length > 0) {
								var address = result.results[0].formatted_address.split(/,/);
								if (address.length === 3 && (address[2] && address[2].trim() === 'USA')) {
									var state = address[1].split(/ /);
									this.model.city = address[0];
									this.model.state = address[1].split(/ /)[1];
								}
							}
						}
					}
				});
		}
		this.isValidAddress(); 
	}

	showLabel() {
		if(this.metaData !== undefined){
			return (this.metaData.label!== undefined && this.metaData.label.length > 0);
		}
		return false;
	}

	showPhoneField(){
		if(this.metaData !== undefined){
			return (!this.metaData.isShort && this.metaData.phoneNumber.show); 
		}
		return false;
	}

	inputRequiredStyle(hasFocus : boolean, value: any, isRequired : boolean):string {
		let css: string = "";
		if (hasFocus === true) {
			css = "bg-border-color";
		}
		
		if(isRequired && value !== undefined){
			if (hasFocus === false && value.trim().length === 0) {
				css = css + " address-inValidBorderBottom";
			}
		}
		return css;
	} 

	setCustomborderStyle(hasFocus: boolean, style: {}): any {
		return hasFocus ? style : { 'border-bottom': DEFAULT_VALUES.DEFAULT_BORDER_BOTTOM }
	}

	setLabelColor(hasFocus: boolean, style: {}): any {
		return hasFocus ? style : { 'color': DEFAULT_VALUES.DEFAULT_LABEL_COLOR };
	}
	
}




