import { Component, OnInit, Input } from '@angular/core';

@Component({
    selector: 'loader',
    templateUrl: './loader.component.html',
    styles: [`.loaderbg {
    background-image: url("./assets/images/ajax-loader.gif");
    background-repeat: no-repeat;
    position: relative;
    top: 35%;
    left: 45%;
    height: 32px;
    padding-left: 36px;
    padding-top: 5px;
    width: 214px;
}
.loader {
    position: relative;
    top: 35%;
    left: 44%;
    height: 32px;
    
    padding-top: 5px;
    width: 214px;
}

.loaderContainer {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba(255, 255, 255, 0.8);
    top: 0px;
    z-index: 100;
    left: 0px;
}`]
})
export class LoaderComponent implements OnInit {
    @Input() loaderMessage: any;
    messageToShow: String;

    constructor() { }

    ngOnInit() {
        this.messageToShow = this.loaderMessage || "Loading...";
    }

}



