import { Component, OnInit, AfterViewInit, Input, ViewChild, HostListener } from '@angular/core';
import { APPLICATION_PATH, StaticDataSource, HttpPostRequest } from "../../../../models/static.model";
import { HttpService } from "../../../../services/http.service";
import { IFrameCommunication } from "../../../../services/iFrameCommunication.service";
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { HttpHeaders, HttpParams } from '@angular/common/http';
import { HttpClient } from '@angular/common/http';
import {Injectable} from "@angular/core";
import {Observable} from "rxjs/Rx";
import { Router } from '@angular/router';
export class vizItem {    
  vizUrl: string; 
  vizIndex:string;   
  feed:boolean;

  constructor(message: string, id: string)    
  {    
    this.vizUrl = message;    
    this.vizIndex = id
    this.feed=false;
  }    
}

@Component({
  selector: 'app-analytics',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.scss']
})
export class MainComponent  {
  @Input() visualizations: vizItem[];
  @Input() feed: boolean;
  @Input() inbox: boolean;
  @Input() decisionfactor:string;
  @Input() heading: string;
  constructor(private _router: Router)
  {
     
  }
  ngOnInit(    
  ) {

  this.visualizations = [  
  new vizItem('https://public.tableau.com/views/10_0InternationalTourism/InternationalTourism?:embed=y&:tooltip=n&:toolbar=n&:showVizHome=no','2') ,
  new vizItem('https://public.tableau.com/views/USMassShooting2013-2015TrendsPerState/Story1?:embed=y','1')

];  
}


}


