
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { Routes } from '@angular/router';
import { APP_BASE_HREF } from '@angular/common';
import { HttpModule } from '@angular/http';
import { MainComponent } from './main/main.component';
import { SharedComponentModule } from '../../sharedModule/v1/sharedComponent.module';
import { StaticDataSource } from "../../../models/static.model";
import { TableauFeedComponent } from './main/tableaufeed.component';
import { TableauViewComponent } from '../../../modules/analytics/v1/components/tableau/tableauView.component';
import { TableauSearchComponent } from './components/search/tableausearch.component';
import { DataService } from './service/data.service';
import { TableauAlertComponent } from './components/alert/tableauAlert.component';

const AnalyticsRoutes: Routes = [
  { path: 'analytics',  component: TableauViewComponent },
  { path: 'tableauInbox', component: TableauViewComponent },
  { path: 'tableausearch', component: TableauViewComponent },
  { path: 'tableaualert', component: TableauSearchComponent },
  { path: 'tableauinsight', component: TableauViewComponent },
  { path: 'tableaufeed',  component: TableauViewComponent}
];
@NgModule({
  imports: [
    BrowserModule,
    HttpModule, SharedComponentModule,
    RouterModule.forRoot(AnalyticsRoutes)
  ],
  declarations: [MainComponent],
  providers: [StaticDataSource,DataService, { provide: APP_BASE_HREF, useValue: '/' }],
  bootstrap: [MainComponent]
})
export class AnalyticsModule { }



