import { MetaData } from './common/common.model';


export interface SelectedItem{
    name:string;
    code:string;
}

export class Address {
    public firstName: string = "";
    public lastName: string = "";
    public company: string = "";
    public address: string = "";
    public city: string = "";
    public state: string = "";
    public zip: string = "";
    public country: SelectedItem;
    public phoneNumber: string = "";
    constructor() {
        this.country = {name:'USA', code:'US'};
    }
}

export class BillToAddress extends Address {
    public faxNumber: string = "";
}

export class ShipToAddress extends Address {
    
}

export class AddressMetaData {
    public label: string ="";
    public isShort: boolean =false;
    public firstName: MetaData;
    public lastName: MetaData;
    public company: MetaData;
    public address: MetaData;
    public city: MetaData;
    public state: MetaData;
    public zip: MetaData;
    public country: MetaData;
    public phoneNumber: MetaData;
    constructor() {
        this.firstName = new MetaData();
        this.lastName = new MetaData();
        this.company = new MetaData();
        this.address = new MetaData();
        this.city = new MetaData();
        this.state = new MetaData();
        this.zip = new MetaData();
        this.country = new MetaData();
        this.phoneNumber = new MetaData();
        this.initializeAddress();
    }

    private initializeAddress(): void {
        this.isShort = false;
        this.firstName.label = "First Name";
        this.firstName.placeholder = "First Name";
        this.lastName.label = "Last Name";
        this.lastName.placeholder = "Last Name";
        this.company.label = "Company";
        this.company.placeholder = "Company";
        this.address.label = "Street Address";
        this.address.placeholder = "Street Address";
        this.city.label = "City";
        this.city.placeholder = "City";
        this.state.label = "State";
        this.state.placeholder = "State";
        this.zip.label = "Zip";
        this.zip.placeholder = "Zip";
        this.country.label = "Country";
        this.country.placeholder = "Country";
        this.phoneNumber.label = "Phone Number";
        this.phoneNumber.placeholder = "Phone Number";
    }
}

export class BillToAddressMetaData extends AddressMetaData {
    
    public faxNumber: MetaData;

    constructor() {
        super();
        this.faxNumber = new MetaData();

        this.initializeBillToAddress();
    }

    private initializeBillToAddress(): void {
        this.faxNumber.label = "Fax";
        this.faxNumber.placeholder = "Fax";
        this.country.label = "Billing Country"
    }   
}

export class ShipToAddressMetaData extends AddressMetaData {
    constructor() {
        super();
        this.initializeShipToAddress();
    }
    private initializeShipToAddress(): void {
        this.country.label = "Shipping Country"
    } 
}
  
export class Country {
    public name: string;
    public code: string;
    public codeThree: string;

    constructor(name: string, code: string) {
        this.name = name;
        this.code = code;
    }
}



