import { Injectable } from '@angular/core';

export class vizItem {    
    vizUrl: string; 
    vizIndex:string;   
    constructor(message: string, id: string)    
    {    
      this.vizUrl = message;    
      this.vizIndex = id;
    }    
  }
@Injectable()
export class DataService {
 
    static visualizations : vizItem[]
      
    constructor() {
   
    }

   
}


